<script>
  import PublicHeader from '@/components/PublicHeader.vue';

  export default {

    name: 'pay-success',
    components: {
      PublicHeader
    },
    data() {
      return {
        loading: false,
        resendTip: false,
        secondNum: 60,
        packageInfo: {
          account: String,
          packages: String,
          cycle: String,
          originalPrice: String,
          reduce: String,
          preferentialPrice: String,
          aff: String,
          paymentMethod: String,
          productId: String,
          billingcycle: String,
          redirectTo: String,
          configoptions: {}
        },
        isAndroid: false,
        isIos: false,
        isPc: true
      };
    },
    created() {
      this.packageInfo.account = this.$route.params.account;
      this.packageInfo.packages = this.$route.params.packages;
      this.packageInfo.cycle = this.$route.params.cycle;
      this.packageInfo.originalPrice = this.$route.params.originalPrice;
      this.packageInfo.reduce = this.$route.params.reduce;
      this.packageInfo.preferentialPrice = this.$route.params.preferentialPrice;
      this.packageInfo.aff = this.$route.query['aff'] || localStorage.getItem('aff') || '';
      this.packageInfo.paymentMethod = this.$route.params.paymentMethod;
      this.packageInfo.productId = this.$route.params.productId;
      this.packageInfo.billingcycle = this.$route.params.billingcycle;
      this.packageInfo.redirectTo = this.$route.params.redirectTo;
      this.packageInfo.configoptions = this.$route.params.configoptions;
    },
    mounted() {
      this.isAndroid = !!(navigator.userAgent.toLowerCase()
        .indexOf('android') > 1);
      this.isIos = !!navigator.userAgent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      this.isPc = !this.isAndroid && !this.isIos;
      // const that = this
      // setInterval(function () {
      //   that.$request.getUsersInvoices({}, function (data) {
      //     console.log(data)
      //   })
      // }, 2000)
    },
    methods: {
      resendEmail() {

        this.loading = true;
        this.$http.post(`${process.env.VUE_APP_SERVER_URL}/api/v1/orders/quickbuy`, {
          'email': this.packageInfo.account,
          'paymentMethod': this.packageInfo.paymentMethod,
          'productId': this.packageInfo.productId,
          'billingcycle': this.packageInfo.billingcycle,
          'aff': this.packageInfo.aff,
          'redirectTo': this.packageInfo.redirectTo,
          'configoptions': this.packageInfo.configoptions
        })
          .then(
            (data) => {
              this.loading = false;
              this.resendTip = true;
              let test = setInterval(() => {
                this.secondNum--;
                if (this.secondNum === 0) {
                  window.clearInterval(test);
                  this.resendTip = false;
                  this.secondNum = 60;
                }
              }, 1000);
              // if (data && (data.status === 200 || data.status === 201)) {
              //
              // } else {
              //   this.errorDisplay = true;
              //   this.errorDescription = '出現錯誤，請稍後重試';
              // }
            });
      },
      openZendesk() {
        window.zE('webWidget', 'toggle');
      }
    }
  };

</script>

